section {
  margin-bottom: 20px;
}

header {
  top: 0;
  left: auto;
  right: 0;
  position: fixed;

  width: 100%;
  display: flex;
  z-index: 1100;
  box-sizing: border-box;
  flex-shrink: 0;
  flex-direction: column;

  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);

  &.appbar-fixed {
    position: fixed;
  }
  &.appbar-relative {
    position: relative;
  }
  &.appbar-absolute {
    position: absolute;
  }
  .header {
    display: flex;
    position: relative;
    align-items: center;
    padding-left: 24px;
    padding-right: 24px;
    background-color: $panel-background;

    min-height: 64px;

    /* Larger than mobile */
    @media (min-width: 400px) {
      min-height: 48px;
      padding-left: 16px;
      padding-right: 16px;
    }

    /* Larger than phablet (also point when grid becomes active) */
    @media (min-width: 550px) {
      min-height: 56px;
      padding-left: 16px;
      padding-right: 16px;
    }
  }

  .app-bar-divider {
    flex: 1 1 auto;
  }

  .app-bar-right {
    display: inline-flex;
  }
}

.box {
  background-color: $panel-background;
  border-radius: 4px;
  box-shadow: 0px 3px 3px -2px rgba(0, 0, 0, 0.2),
    0px 3px 4px 0px rgba(0, 0, 0, 0.14), 0px 1px 8px 0px rgba(0, 0, 0, 0.12);
  padding: 16px;

  &.box-no-shadow {
    box-shadow: none;
  }

  &.box-rect {
    border-radius: 0;
  }
}
